<template>
  <el-dialog
    title="数据导入"
    v-model.sync="centerDialogVisible"
    width="70%"
    center
    @close="close"
  >
    <el-upload
      class="upload-box"
      drag
      :accept="json?'.json':'.xml'"
      action
      :file-list="uploadFiles"
      :on-change="fileChange"
      :auto-upload="false"
      :limit="1"
      :on-exceed="exceedFile"
    >
      <i class="el-icon-upload"></i>
      <div class="el-upload__text">
        将文件拖到此处，或
        <em>点击上传</em>
        <div slot="tip" class="el-upload__tip">
          只能上传
          <em>{{json?'json':'xml'}}</em> 文件，且最大文件数量不超过 <em>1</em> 个
        </div>
      </div>
    </el-upload>
    <div style="display: flex">
      <pre style="width:100%;word-break: break-all;white-space: pre-line;max-height: 300px;overflow-y: auto;"> {{currentNodeData}} </pre>
    </div>
    <span slot="footer" class="dialog-footer">
      <el-button @click="cancelUpload">取 消</el-button>
      <el-button type="primary" @click="confirmUpload"
        >导入</el-button
      >
    </span>
  </el-dialog>
</template>
<script>
export default {
  props: {
    'graphXml':{
      type:String,
      default:''
    },
    'json':{
      type:Boolean,
      default:false
    }
  },
  data () {
    return {
      uploadFiles: [],
      centerDialogVisible: false,
      currentNodeData: "",
    }
  },
  components: {
  },
  watch: {
    currentNodeData: {
      handler (newvalue) {
        //TODO
      },
      immediate: true,
      deep: true
    }
  },
  mounted () {
    console.log(this.json)
    console.log(666)
    this.centerDialogVisible = true
    this.currentNodeData = this.graphXml
  },
  methods: {
    close () {
      this.centerDialogVisible = false
      if(this.json){
        this.$parent.uploadJSONDataVisible = false;
      }else{
        this.$parent.uploadDataVisible = false;
      }
    },
    confirmUpload () {
      if(!this.currentNodeData){
        this.$message.warning('请上传文件')
        return
      }
      // 触发父组件重绘函数
      this.$emit("uploadPaintFlow", this.currentNodeData)
      this.centerDialogVisible = false
      if(this.json){
        this.$parent.uploadJSONDataVisible = false;
      }else{
        this.$parent.uploadDataVisible = false;
      }
    },
    cancelUpload () {
      console.log(this.currentNodeData)
      this.centerDialogVisible = false
      this.$parent.uploadDataVisible = false;
      this.currentNodeData = ""
    },

    tirggerFile () {

    },
    fileChange (file) {
      // 获取文件类型
      if(this.json){
        var fileType = file.name.split(".").pop()
        if (fileType !== 'json') {
          this.$message.error("上传文件类型错误,请重新上传JSON类型的文件")
          this.uploadFiles = []
          return
        }
      }else{
        var fileType = file.name.split(".").pop()
        if (fileType !== 'xml') {
          this.$message.error("上传文件类型错误,请重新上传XML类型的文件")
          this.uploadFiles = []
          return
        }
      }
      if (file) {
        let reader = new FileReader()
        reader.readAsText(file.raw) //注意读取中文的是用这个编码，不是utf-8
        reader.onload = () => {
          this.$message.success("文件上传成功")
          var result = reader.result;
          this.currentNodeData = result
        }
      }
    },
    exceedFile () {
      this.$message.error("超出文件可上传数量,请先清除文件")
    }
  },
}
</script>
