import uploadData from "./uploadData.vue"
export default {
  name: "dragFlow",
  components: {
    uploadData,
  },
  computed: {
    // generalToolbarItems: () => generalToolbarItems,
  },
  mounted() {
    this.init();
  },
  props:{
    generalToolbarItems:{
      type:Array,
      default:()=>{
        return generalToolbarItems
      }
    }
  },
  data() {
    return {
      editor: null,
      graph: null,
      aaa: {},
      searchValue:'',

      uploadDataVisible: false,
      graphXml: '',
      uploadJSONDataVisible:false,
      graphJSON:''
    };
  },
  methods: {
    init() {
      this.createGraph();
      this.eventCenter();
      this.$nextTick(() => {
        this.initGeneralTool();
      });
    },
    createGraph() {
      this.editor = new mxEditor();
      let graph = this.editor.graph;
      this.graph = graph;
      // console.log(this.$refs.dfcontainer);
      this.editor.setGraphContainer(this.$refs.dfcontainer);
      // this.graph.setConnectable(true);
      // return
      // this.graph.setEnabled(false);
      this.graph.setCellsResizable(false);
      this.graph.setConnectable(true);
      this.graph.setMultigraph(false);
      this.graph.setAllowDanglingEdges(false);
      this.graph.setAllowLoops(false);
      let EdgeStyle = this.graph.getStylesheet().getDefaultEdgeStyle();
      EdgeStyle[mxConstants.STYLE_EDGE] = mxEdgeStyle.TopToBottom;
      EdgeStyle[mxConstants.STYLE_STROKEWIDTH] = 2; 
      EdgeStyle[mxConstants.STYLE_ROUNDED] = 1; 
      this.iconSet();
    },
    iconSet() {
      function mxIconSet(state) {
        this.images = [];
        var graph = state.view.graph;
        // Delete
        var img = mxUtils.createImage(imgsrc);
        // console.log(img);
        img.setAttribute("title", "Delete");
        img.style.position = "absolute";
        img.style.cursor = "pointer";
        img.style.width = "16px";
        img.style.height = "16px";
        img.style.left = state.x + state.width + "px";
        img.style.top = state.y - 16 + "px";

        mxEvent.addGestureListeners(
          img,
          mxUtils.bind(this, function (evt) {
            // Disables dragging the image
            mxEvent.consume(evt);
          })
        );

        mxEvent.addListener(
          img,
          "click",
          mxUtils.bind(this, function (evt) {
            graph.removeCells([state.cell]);
            mxEvent.consume(evt);
            this.destroy();
          })
        );

        state.view.graph.container.appendChild(img);
        this.images.push(img);
      }
      mxIconSet.prototype.destroy = function () {
        if (this.images != null) {
          for (var i = 0; i < this.images.length; i++) {
            var img = this.images[i];
            img.parentNode.removeChild(img);
          }
        }

        this.images = null;
      };
      mxConnectionHandler.prototype.connectImage = new mxImage(
        createImg,
        16,
        16
      );
      var iconTolerance = 20;
      let graph = this.graph;
      this.graph.addMouseListener({
        currentState: null,
        currentIconSet: null,
        mouseDown: function (sender, me) {
          if (this.currentState != null) {
            this.dragLeave(me.getEvent(), this.currentState);
            this.currentState = null;
          }
        },
        mouseMove: function (sender, me) {
          if (
            this.currentState != null &&
            (me.getState() == this.currentState || me.getState() == null)
          ) {
            var tol = iconTolerance;
            var tmp = new mxRectangle(
              me.getGraphX() - tol,
              me.getGraphY() - tol,
              2 * tol,
              2 * tol
            );

            if (mxUtils.intersects(tmp, this.currentState)) {
              return;
            }
          }
          var tmp = graph.view.getState(me.getCell());

          // Ignores everything but vertices
          if (
            graph.isMouseDown ||
            (tmp != null && !graph.getModel().isVertex(tmp.cell))
          ) {
            tmp = null;
          }

          if (tmp != this.currentState) {
            if (this.currentState != null) {
              this.dragLeave(me.getEvent(), this.currentState);
            }

            this.currentState = tmp;

            if (this.currentState != null) {
              this.dragEnter(me.getEvent(), this.currentState);
            }
          }
        },
        mouseUp: function (sender, me) {},
        dragEnter: function (evt, state) {
          if (this.currentIconSet == null) {
            this.currentIconSet = new mxIconSet(state);
          }
        },
        dragLeave: function (evt, state) {
          if (this.currentIconSet != null) {
            this.currentIconSet.destroy();
            this.currentIconSet = null;
          }
        },
      });
      // Enables rubberband selection
      new mxRubberband(graph);
    },
    eventCenter() {
      this.cellsEvent();
      this.mouseEvent();
      this.keyEvent();
    },
    // 删除节点
    deleteNode () {
      var cells = this.graph.getSelectionCells();
      this.graph.removeCells([...cells]);
    },
    keyEvent(){
      let keyHandler = new mxKeyHandler(this.graph);
      // console.log('keyHandler')
      // console.log(keyHandler)
      //监听绑定所以按键（此方法会覆盖keyHandler.bindKey绑定特定事件的方法）
      // keyHandler.getFunction = function(evt){
      //   console.log(evt)
        
      // };
      //监听特定按键的方法
      keyHandler.bindKey(46, (evt)=> {
        // console.log(555)
        // console.log(evt)
        this.deleteNode();
      });

    },
    cellsEvent() {
      this.graph.addListener(mxEvent.ADD_CELLS, (sender, evt) => {
        this.$nextTick(() => {
          let addCell = evt.properties.cells[0];
          // console.log('addCell',addCell);
          let isEdge = addCell.edge;
          if(isEdge){
            this.$emit('addConnection',addCell.id)
          }else{
            this.$emit('addNode',addCell.id)
          }
          
        });
      });
      this.graph.addListener(mxEvent.CELLS_REMOVED, (sender, evt) => {
        this.$nextTick(() => {
          let removeCells = evt.properties.cells;
          // console.log('removeCell',removeCells);
          let output = {
            node:[],
            connection:[],
          };
          removeCells.forEach(item=>{
            if(item.edge){
              output.connection.push(item.id);
            }else{
              output.node.push(item.id);
            }
          })
          this.$emit('removeNode',output)
        });
      });
    },
    mouseEvent() {
      this.graph.addMouseListener({
        mouseDown: (sender, evt) => {
          // console.log('down',evt)
          this.upCell = {};
          if (!evt.state) {
            //点在画布上了
          } else if (evt.state.cell&&evt.state.cell.edge) {
            //连线 节点
          }else{
            // console.log('记录当前点击 节点信息')
            //非连线 节点
            //1.记录当前点击 节点信息。在up事件中比较是否为同一目标。
            this.upCell = evt.state.cell;
          }
        },
        mouseMove: (sender, me) => {
          //TODO
        },
        mouseUp: (sender, evt) => {
          // console.log('up',evt)

          if (evt.sourceState === undefined) {
            setTimeout(()=>{
              let consumed = evt.evt.isConsumed;
              if(!consumed){
                // console.log('canvasClick');
                this.$emit('canvasClick')
              }
            },0)
            return false;
          } else {
            var cell = evt.sourceState.cell;
            // console.log('cell',cell)
            if (cell) {
              //点击到节点
              if (cell.edge && cell.edge === true) {
                //连线
              } else if (cell.vertex && (cell.vertex === true||cell.vertex === 1)) {
                //顶点/节点
                if(cell.id==this.upCell.id){
                  // console.log('nodeClick')
                  this.$emit('nodeClick',cell.id)
                }
              } else {
                //TODO
              }
            } else {
              
            }
          }
        }
      });
      mxEvent.addMouseWheelListener((evt, up) => {
        if(evt.target.parentNode!=this.$refs.dfcontainer)return;
        if (up) {
          this.graph.zoomIn();
        } else {
          this.graph.zoomOut();
        }
      });
    },
    initGeneralTool() {
      var generalToolbarDomArray = this.$refs.generalToolItems;
      if (
        !(
          generalToolbarDomArray instanceof Array ||
          generalToolbarDomArray.length <= 0
        )
      ) {
        return;
      }
      generalToolbarDomArray.forEach((dom, domIndex) => {
        var toolItem = this.generalToolbarItems[domIndex];
        var { width, height } = toolItem;
        var itemClass = toolItem.class;
        var generalDropHandler = (graph, evt, dropCell, x, y) => {
          const drop = !R.isNil(dropCell);
          const realX = drop ? x - dropCell.geometry.x : x;
          const realY = drop ? y - dropCell.geometry.y : y;
          const { width, height } = toolItem;
          const styleObj = toolItem.style;
          const style = Object.keys(styleObj)
            .map((attr) => `${attr}=${styleObj[attr]}`)
            .join(";");
          const parent = drop ? dropCell : this.graph.getDefaultParent();
          this.graph.getModel().beginUpdate();
          try {
            let vertex = this.graph.insertVertex(
              parent,
              null,
              toolItem.value,
              realX - width / 2,
              realY - height / 2,
              width,
              height,
              style + ";whiteSpace=wrap;word-break=break-all"
            );
            vertex.title =
              `<div style='word-break:break-all'>` +
              toolItem["title"] +
              "</div>";
            vertex.dropAble = toolItem["dropAble"];
            vertex.id = toolItem["id"] + "-" + this.getUUID(32, 16);
            vertex["isGroup"] = toolItem["id"].includes("group") ? true : false;
            //TODO extra
          } finally {
            this.graph.getModel().endUpdate();
          }
        };
        var generalcreateDragPreview = () => {
          var elt = document.createElement("div");
          elt.style.width = `${width}px`;
          elt.style.height = `${height}px`;
          elt.style.transform = "translate(-50%,-50%)";
          elt.className = itemClass;
          return elt;
        };
        let ds = mxUtils.makeDraggable(
          dom,
          this.graph,
          generalDropHandler,
          generalcreateDragPreview(),
          0,
          0,
          true,
          true
        );
        ds.setGuidesEnabled(true);
      });
    },
    getUUID(len, radix) {
      let chars = '0123456789ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz'.split('');
      let uuid = [], i;
      radix = radix || chars.length;
      if (len) {
        for (i = 0; i < len; i++) uuid[i] = chars[0 | Math.random() * radix];
      } else {
        let r;
        uuid[8] = uuid[13] = uuid[18] = uuid[23] = '-';
        uuid[14] = '4';
        for (i = 0; i < 36; i++) {
          if (!uuid[i]) {
            r = 0 | Math.random() * 16;
            uuid[i] = chars[(i == 19) ? (r & 0x3) | 0x8 : r];
          }
        }
      }
      return uuid.join('');
    },
    inPutXml(){
      // console.log(5555)
      this.uploadDataVisible = true;
      this.graphXml = '';
    },
    outPutXml(){
      var encoder = new mxCodec();
        var node = encoder.encode(this.graph.getModel());
      // console.log(node)
      let xmlStr = mxUtils.getPrettyXml(node)
      // console.log(xmlStr)
      let bb = new Blob([xmlStr], {type: 'text/plain'}); 
      let xmlStrUrl = window.URL.createObjectURL(bb)
      // mxUtils.popup(mxUtils.getPrettyXml(node), true); 
      var downloadAnchorNode = document.createElement('a')
      downloadAnchorNode.setAttribute("href", xmlStrUrl);
      downloadAnchorNode.setAttribute("download", 'data.xml')
      downloadAnchorNode.click();
      downloadAnchorNode.remove();
      this.$message.success("正在下载中,请稍后...")
      return
      var datastr = "data:text/json;charset=utf-8," + encodeURIComponent(this.currentNodeData);
      var downloadAnchorNode = document.createElement('a')
      downloadAnchorNode.setAttribute("href", datastr);
      downloadAnchorNode.setAttribute("download", 'data.xml')
      downloadAnchorNode.click();
      downloadAnchorNode.remove();
      this.$message.success("正在下载中,请稍后...")
      // document.body.appendChild(mxUtils.button('View XML', ()=>{
      //   var encoder = new mxCodec();
      //   var node = encoder.encode(this.graph.getModel());
      //   mxUtils.popup(mxUtils.getPrettyXml(node), true);           
      // })); 
    },
    uploadPaintFlow (newvalue) {
      this.graph.selectAll();
      this.graph.removeCells(this.graph.getSelectionCells());
      setTimeout(() => {
        this.decode(newvalue, this.graph);
        this.$message.success('渲染成功');
      }, 1000);
    },
    decode (graphXml, graph) {
      // console.log('decode')
      try {
        //1
        var xmlDoc = this.createXmlDom(graphXml);
        // console.log(xmlDoc)
        var node = xmlDoc.documentElement;
        var dec = new mxCodec(node.ownerDocument);
        // console.log(dec)
        dec.decode(node, graph.getModel());
      } finally {
        graph.getModel().endUpdate();
        this.autoSize()
      }
    },
    createXmlDom (str) {
      return (new DOMParser()).parseFromString(str, 'text/xml');
    },
    autoSize () {
      // console.log('resize')
      this.graph.zoomActual();
      this.graph.fit();
      this.graph.center();
      this.graph.zoomTo(0.9)
    },
    toSvgXml(){
      var xmlDoc = mxUtils.createXmlDocument();
      var root = xmlDoc.createElement('svg');  //svg root
      root.setAttribute('xmlns', mxConstants.NS_SVG);
      xmlDoc.appendChild(root);
      var xmlCanvas = new mxSvgCanvas2D(root);
      var imgExport = new mxImageExport();
      // make graph to xmlDoc
      imgExport.drawState(this.graph.getView().getState(this.graph.model.root), xmlCanvas);
    
      // console.log(root)
      var svgXml = mxUtils.getXml(root);
      return svgXml
    },
    outPutImage(evt,fileName='workflow',imgType='png'){
      const xmlStr = this.toSvgXml();
      var img = document.createElement('img');
      var bounds = this.graph.getGraphBounds();
      // console.log(bounds)
      img.setAttribute('width',bounds.width+Math.abs(bounds.x))
      img.setAttribute('height',bounds.height+Math.abs(bounds.y))
      let base64Str = 'data:image/svg+xml;base64,'+ btoa(unescape(encodeURIComponent(xmlStr)))
      img.setAttribute('src', base64Str);
      // console.log(base64Str)
      document.documentElement.appendChild(img)
      img.onload = function(){
          var canvas = document.createElement('canvas');
          var context = canvas.getContext("2d");
          canvas.width = img.width;
          canvas.height = img.height;
          context.drawImage(img, 0, 0);
          var canvasData = canvas.toDataURL("image/" + imgType);
          var img2 = document.createElement('img');
          img2.setAttribute('src',canvasData)
          img2.onload = function(){
            var a = document.createElement("a");
            a.download = fileName + "." + imgType;
            a.href = img2.getAttribute('src');
            a.click();
          }   
      };
    },
    outPutToSvg(fileName='model'){
      let svg  = this.toSvgXml();
      const blob = new Blob([svg], { type: 'image/svg+xml' });
      const url = URL.createObjectURL(blob);
      let link = document.createElement('a');
      link.href = url;
      link.download = fileName + '.svg';
      link.click();
    },
    outPutJSON(ev,fileName='df'){
      let jsonObj = this.xml2JSON();
      let jsonStr = JSON.stringify(jsonObj);
      const blob = new Blob([jsonStr], { type: 'application/json' });
      const url = URL.createObjectURL(blob);
      let link = document.createElement('a');
      link.href = url;
      link.download = fileName + '.json';
      link.click();

    },
    xml2JSON(){
      var encoder = new mxCodec();
      var node = encoder.encode(this.graph.getModel());
      const xml = mxUtils.getXml(node);
      const jsonObj = $x2js.xml2js(xml);
      // console.log(xml)
      // console.log(jsonObj)
      return jsonObj
    },
    inPutJSON(){
      this.uploadJSONDataVisible = true;
      this.graphJSON = '';
    },
    uploadJSONPaintFlow(newvalue) {
      // console.log(newvalue)
      // return
      const xml = $x2js.js2xml(JSON.parse(newvalue))
      // console.log(xml)
      this.graph.selectAll();
      this.graph.removeCells(this.graph.getSelectionCells());
      setTimeout(() => {
        this.decode(xml, this.graph);
        this.$message.success('渲染成功');
      }, 1000);
    },
    /**
     * components use
     */
    getFlowNodes(){
      let jsonObj = this.xml2JSON();
      let cellArr = jsonObj.mxGraphModel&&jsonObj.mxGraphModel.root&&jsonObj.mxGraphModel.root.mxCell||[];
      let output = {
        total:[],
        node:[],
        connection:[],
      };
      cellArr.forEach(item=>{
        if(item._id&&item.mxGeometry){
          output.total.push(item);
          if(item._edge){
            connection.push(item);
          }else{
            connection.node.push(item);
          }
        }
      })
      return output
    },
    recoverFlow(totalCell=[],callback=null){
      let transTarget = {
        mxGraphModel:{
          root:{
            mxCell:[
              {"_id": "0"},
              {"_id": "1","_parent":"0"},
              ...totalCell
            ]
          }
        }
      };
      const graphXml = $x2js.js2xml(transTarget);
      this.graph.selectAll();
      this.graph.removeCells(this.graph.getSelectionCells());
      this.decode(graphXml, this.graph);
      if(callback&&typeof callback =='function'){
        callback();
      }
    },
    // 修改节点文本内容
    updateNodeName (value,cellId='') {
      let target;
      if(cellId){
        // console.log(this.graph.getModel().cells)
        let cellArr = Object.values(this.graph.getModel().cells)
        target = cellArr.find(item=>item.id===cellId);
      }else{
        var cell = this.graph.getSelectionCells();
        target = cell[0];
      }
      target&&this.graph.cellLabelChanged(target, value);
    },
  },
};
</script>

